@media screen and (min-width: 800px) {
  .hero {
    margin-bottom: 1rem;
    width: 100%;
    min-height: 95vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }
  .container {
    width: 50%;
    padding: 3rem;
    padding-top: 2rem;
    margin-top: 4rem;
  }

  .start-img {
    width: 90%;
  }

  .star-container {
    display: none;
  }

  .arrow {
    bottom: 0;
  }
  .welcome-letter {
    padding: 1rem;
    justify-content: unset;
    align-items: unset;
    margin-top: 1rem;
  }

  .welcome-letter h1,
  .welcome-letter h2,
  .welcome-letter button,
  .text {
    margin: 20px;
  }
  .welcome-letter .text-container {
    width: 70%;
    padding: 5rem 10%;
    margin: 10px;
    margin-top: unset;
    display: unset;
    flex-direction: unset;
    justify-content: unset;
  }

  .welcome-letter .microphone-container {
    width: 35%;
    position: relative;
  }

  .microphone-container {
    display: block;
  }

  .welcome-letter h1 {
    font-size: 6.5rem;
  }

  .welcome-letter h2 {
    font-size: 2.8rem;
  }

  .welcome-letter p {
    font-size: 20px;
    letter-spacing: 1px;
    line-height: 20px;
    font-weight: 400;
  }

  .text-container .buttons {
    flex-direction: row;
    align-items: unset;
  }
  .welcome-letter button {
    width: unset;
    background-color: var(--color-blue);
    border: none;
    box-shadow: unset;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.5rem;
    color: white;
    margin-top: 4rem;
    padding: 15px 30px;
  }

  .welcome-letter button:hover {
    background-color: white;
    color: var(--color-blue);
    cursor: pointer;
  }

  .microphone {
    position: absolute;
    overflow: visible;
    width: 56px;
    height: 131px;
  }
  .microphone-red {
    z-index: 1000;
    right: 32%;
    bottom: 10%;
    transform: scale(1.3);
    display: block;
  }

  .mic-one {
    left: 150px;
    top: 120px;
    transform: rotate(25deg);
  }

  .mic-two {
    transform: scale(0.7) rotate(130deg);
    animation: mic-two 880ms infinite;
  }

  .mic-three {
    left: 70%;
    top: 0;
    transform: scale(0.7) rotate(-25deg);
  }

  .mic-four {
    left: 12px;
    top: 40%;
    transform: scale(0.7) rotate(-90deg);
  }

  .mic-five {
    top: 40%;
    left: 60%;
    transform: rotate(-45deg);
    animation: mic-five 880ms infinite;
  }

  .mic-six {
    top: 62%;
    left: 40%;
    transform: scale(0.7) rotate(120deg);
  }

  .mic-seven {
    top: 70%;
    transform: rotate(-10deg);
    animation: note 880ms infinite;
  }

  .mic-eight {
    top: 80%;
    left: 70%;
    transform: rotate(50deg);
  }

  .program {
    width: 100%;
    padding: 2rem;
    min-height: 90vh;
    display: flex;
    flex-direction: row-reverse;
    align-items: unset;
  }

  .program-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 1rem;
  }
  .program-card {
    width: 50%;
    justify-content: flex-end;
  }

  .program-card img {
    animation: none;
  }

  .pic-container {
    justify-content: flex-end;
  }

  .program h2 {
    text-align: left;
    font-size: 80px;
    letter-spacing: 2px;
  }

  .program li {
    font-size: 30px;
  }

  .program-card svg {
    transform: scale(2.1);
  }

  .hand-one {
    left: 125px;
    bottom: 300px;
    transform: scale(2) rotate(170deg);
  }

  .hand-two {
    bottom: 450px;
    left: 630px;
    transform: rotate(-20deg) scale(2);
  }
  footer {
    display: flex;
    padding: 5rem 7rem;
  }
  footer div {
    margin: 2rem;
    margin-right: 3rem;
  }
  footer div h3 {
    font-size: 2.2rem;
    margin-bottom: 1rem;
  }

  footer div a {
    text-decoration: none;
    color: var(--color-blue);
  }

  footer p,
  footer a {
    font-size: 1.2rem;
    line-height: 26px;
  }

  .bottle {
    position: absolute;
    transform: scale(1.3);
    display: block;
  }

  .left-hand-bottle {
    left: 18%;
    bottom: 15%;
    animation: left-bottle 880ms infinite;
  }

  .right-hand-bottle {
    left: 25%;
    bottom: 10%;
    animation: right-bottle 880ms infinite;
  }

  .form-container {
    padding: 5rem;
  }
  form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 20px;
    width: 70%;
    font-size: 1.5rem;
  }

  form input {
    width: 75%;
    padding: 5px;
  }

  form button {
    background-color: var(--color-blue);
    border: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.5rem;
    color: white;
    padding: 15px 30px;
    width: 50%;
    justify-self: end;
    margin-top: 10px;
    margin-right: 20px;
  }
}
